
'use server';

import { Resend } from 'resend';
import { adminDb, isAdminReady } from '@/lib/firebase-admin';

const resendApiKey = process.env.RESEND_API_KEY;
const fromEmail = process.env.FROM_EMAIL;

let resend: Resend | null = null;

if (resendApiKey && fromEmail && resendApiKey !== 'YOUR_API_KEY') {
  resend = new Resend(resendApiKey);
} else {
  console.warn('‚ö†Ô∏è [Resend] API Key o correo de origen no configurado. Las notificaciones por email est√°n deshabilitadas.');
}

interface EmailParams {
  to: string;
  subject: string;
  html: string;
}

export async function sendPermitUpdateEmail({ to, subject, html }: EmailParams) {
  if (!resend || !fromEmail) {
    console.log('[Email] Env√≠o de correo omitido por falta de configuraci√≥n.');
    return { success: false, error: 'El servicio de correo no est√° configurado.' };
  }

  try {
    const { data, error } = await resend.emails.send({
      from: `SGTC M√≥vil <${fromEmail}>`,
      to,
      subject,
      html,
    });

    if (error) {
      throw error;
    }

    console.log(`‚úÖ [Email] Correo enviado exitosamente a ${to}. ID: ${data?.id}`);
    return { success: true, data };
  } catch (error) {
    console.error(`‚ùå [Email] Fall√≥ el env√≠o de correo a ${to}:`, error);
    return { success: false, error: 'No se pudo enviar la notificaci√≥n por correo.' };
  }
}

export async function getEmailForUser(userId: string): Promise<string | null> {
  if (!isAdminReady()) return null;
  try {
    const userDoc = await adminDb.collection('users').doc(userId).get();
    if (userDoc.exists) {
      return userDoc.data()?.email || null;
    }
    return null;
  } catch (error) {
    console.error(`Error al obtener el email para el usuario ${userId}:`, error);
    return null;
  }
}
